<template>
  <div class="address">
    <van-nav-bar fixed title="收获地址" left-arrow @click-left="$router.go(-1)" />
    <!-- 地址列表 -->
    <div v-if="addressList.length > 0" class="address-list">
      <div class="address-item" v-for="item in addressList" :key="item.address_id">
        <div class="contacts">
          <span class="name">{{item.name}}</span>
          <span class="phone">{{item.phone}}</span>
        </div>
        <div class="detail-address">
          <span class="region">{{item.region.province}}</span>
          <span class="region">{{item.region.city}}</span>
          <span class="region">{{item.region.region}}</span>
          <span class="detail">{{item.detail}}</span>
        </div>
        <div class="option">
          <div @click="setDefAdds(item.address_id)" class="left">
            <van-icon v-if="defaultId !==item.address_id" name="passed" />
            <van-icon v-else color="red" name="checked" />
            <span>默认</span>
          </div>
          <div class="right">
            <div @click="$router.push(`/createadds/${item.address_id}`)" class="edit">
              <van-icon name="edit" />
              <span>编辑</span>
            </div>
            <div @click="delAdds(item.address_id)" class="del">
              <van-icon name="delete-o" />
              <span>删除</span>
            </div>
          </div>
        </div>
      </div>

    </div>
    <!-- 空地址 -->
    <div v-else class="empty-address">
        <img src="@/assets/empty.png" alt="">
        <div class="tips">
          亲，暂无收获地址
        </div>
    </div>

    <!-- 添加地址按钮 -->
    <div class="add">
       <div class="btn" @click="$router.push('/createadds')">添加新地址</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'AddressPage',
  data () {
    return {

    }
  },
  computed: {
    ...mapState('address', ['addressList', 'defaultId']),
    isLogin () {
      return this.$store.getters.token
    },
    query () {
      return this.$route.query
    }
  },
  created () {
    if (this.isLogin) {
      this.$store.dispatch('address/getAddressList')
      this.$store.dispatch('address/getDefaultAdds')
    }
  },
  methods: {
    ...mapActions('address', ['delAdds']),
    setDefAdds (id) {
      this.$store.dispatch('address/setDefaultAdds', id).then(() => {
        if (Object.keys(this.query).length !== 0) {
          this.$router.replace({ path: '/pay', query: this.query })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.address {
  padding-top: 46px;
  padding-bottom: 100px;
  min-height: 100vh;
  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}

.address-list {
  .address-item{
    display: flex;
    width: 94%;
    margin: 10px;
    padding: 15px 20px;
    font-size: 16px;
    color: #333;
    flex-direction: column;
    border-radius: 8px;
    background: #fff;
    box-shadow: 0 0.5px 2px 0 rgba(0, 0, 0, 0.3);
    .contacts {
      flex:1;
      margin-bottom: 10px;
      .name,.phone {
        margin-right: 8px;
      }
    }
    .detail-address {
      flex:1;
      margin-bottom: 10px;
      .region,.detail {
        margin-right: 8px;
      }
    }
    .option {
      display: flex;
      flex:1;
      justify-content: space-between;
      align-items: flex-end;
      .van-icon {
        margin-right: 5px;
      }
      .edit,.del {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
}
.empty-address {
  padding: 150px 25px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
}

.add {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  .btn {
    height: 50px;
    line-height: 50px;
    margin: 10px 20px;
    text-align: center;
    background: linear-gradient(90deg,#f9211c,#ff6335);
    border-radius: 25px;
    color: #fff;
  }
}

</style>
